	module.exports = {"data":{"challengeNode":{"title":"Problem 177: Integer angled Quadrilaterals","description":["Let ABCD be a convex quadrilateral, with diagonals AC and BD. At each vertex the diagonal makes an angle with each of the two sides, creating eight corner angles.","","For example, at vertex A, the two angles are CAD, CAB.","We call such a quadrilateral for which all eight corner angles have integer values when measured in degrees an \"integer angled quadrilateral\". An example of an integer angled quadrilateral is a square, where all eight corner angles are 45°. Another example is given by DAC = 20°, BAC = 60°, ABD = 50°, CBD = 30°, BCA = 40°, DCA = 30°, CDB = 80°, ADB = 50°.","What is the total number of non-similar integer angled quadrilaterals?","Note: In your calculations you may assume that a calculated angle is integral if it is within a tolerance of 10-9 of an integer value."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-177-integer-angled-quadrilaterals","blockName":"Project Euler","tests":[{"text":"<code>euler177()</code> should return 129325.","testString":"assert.strictEqual(euler177(), 129325, '<code>euler177()</code> should return 129325.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler177() {\n  // Good luck!\n  return true;\n}\n\neuler177();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-178-step-numbers","id":"5900f41e1000cf542c50ff30"},"slug":"/coding-interview-prep/project-euler/problem-177-integer-angled-quadrilaterals"}}

